.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

ul {
  list-style: none;
}

.results {
  display: flex;
  justify-content: center;
}

.results h3 {
  font-size: 2rem;
  padding: 0 3%;
}

.results p {
  font-size: 2rem;
}


img {
  max-width: 200px;
}

.timerOk {
  display: inline-block;
  padding: 2%;
  background-color: green;
}

.timerWrapUp {
  display: inline-block;
  padding: 2%;
  background-color: yellow;
}

.timerDanger {
  display: inline-block;
  padding: 2%;
  background-color: red;
}

footer {
  color: white;
  background-color: black;
  margin: 1% 0;
  padding: 1%;
}

footer a {
  color: white;
  text-decoration: none;
}

footer a:hover,
footer a:focus {
  color: #6056f9;
}

